.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;

  box-shadow: 0 2px 4px 0 #0000000d;
  background-color: rgba(255, 255, 255, 1);
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
}

.logo {
  display: inline-block;
  width: 40px;
  height: 40px;
}

.is-active {
  position: relative;
  color: $blue-color;
  margin-left: 0;

  &::after {
    content: '';
    display: block;
    position: absolute;
    background-color: $blue-color;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 4px;
    border-radius: 8px;
  }
}
